.button-row {
  margin: 0 0 15px 0;
}

.button-row button {
  margin-right: 30px;
}

.stardb-app .row {
  margin-bottom: 30px;
}
.header {

  padding: 20px 10px ;

}
.brand {
  padding: 15px 0 0;
}
.header-ul{
  padding: 15px 0 0;
  flex: 1 1;
  justify-content: flex-end;

}
.header a{
  text-decoration: none;
}
.header ul li {
  list-style: none;
  margin: 0;
  padding: 10px 15px;
