.header {

  padding: 20px 10px ;

}
.brand {
  padding: 15px 0 0;
}
.header-ul{
  padding: 15px 0 0;
  flex: 1;
  justify-content: flex-end;

}
.header a{
  text-decoration: none;
}
.header ul li {
  list-style: none;
  margin: 0;
  padding: 10px 15px;
  border-radius: 3px;

}
.header ul li a {
color: #fefefe;

}

.header ul li:hover {
  background-color: #444;
}

.header a:hover {
  text-decoration: none;
}

.header h3 a {
  color: white;
}

.header h3 a:hover {
  color: #00bc8c;
}

.header button {
  padding: 10px;
  margin: 3px 0 0 10px;
  height: 40px;
}